import React from 'react'

export default function PageNotFound(props) {
    return (
        <div>
            Khng tm thy trang : {props.location.pathname}

            <button className="btn btn-danger" onClick={()=>{
                props.history.replace('/home');
            }}>Bm vo y tr v trang ch</button>
        </div>
    )
}
        